<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <a-alert
                message="功能提示"
                description="需要登录用户审核的留言列表，只拥有审核权限。"
                type="info"
                showIcon
        />
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                :showHeader="true"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from '../留言列表我发起的/components/TopButtons'
    import {getAuditUserCommentList, deleteComment} from '@/api/comment'
    import tableComponent from '@/components/table'

    export default {
        components: {
            TopButtons, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '标题',
                        dataIndex: 'comment_title',
                        key: 'comment_title',
                    },
                    {
                        title: '留言人',
                        dataIndex: 'user_name',
                        key: 'user_name',
                    },
                    {
                        title: '留言对象',
                        dataIndex: 'to_user_names_format',
                        key: 'to_user_names_format',
                    },
                    {
                        title: '审核状态',
                        dataIndex: 'audit_status_format',
                        key: 'audit_status_format',
                    },
                    {
                        title: '更新时间',
                        dataIndex: 'updated_at',
                        key: 'updated_at',
                    },
                    {
                        title: '留言时间',
                        dataIndex: 'leave_at',
                        key: 'leave_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            audit: {
                                visible: true, disable: function (row) {
                                    switch (row.audit_status) {
                                        case 0:
                                            return false;
                                        default:
                                            return true;
                                    }
                                }
                            },
                        }
                    }
                ],
                tableRequest: getAuditUserCommentList,
            }
        },
        methods: {
            handleOperate(type, row) {
                let id = row.id;
                switch (type) {
                    case 'check':
                        this.$router.push({name: '留言查看', params: {id: id}})
                        break;
                    case 'edit':
                        this.$router.push({name: '留言编辑', params: {id: id}})
                        break;
                    case 'audit':
                        this.$router.push({name: '留言审核', params: {id: id}})
                        break;
                    case 'delete':
                        deleteComment(id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;

                }
            },
        },
    }
</script>
